.page-header {
    position: relative;
    margin: 0 auto;
    @media (min-width: $tablet-width) {
        min-width: $tablet-width;
    }
    @media (min-width: $desktop-width) {
        min-width: $desktop-width;
    }
}

.page-header__top {
    position: relative;
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    margin-bottom: -35px;
}

.page-inner-form .page-header__top {
    @media (min-width: $tablet-width) {
        margin-bottom: -34px;
    }
}

.page-header__logo {
    position: static;
    transform: none;
    left: 0;
    top: 0;
    z-index: 2;
    @media (min-width: $tablet-width) {
        z-index: 3;
    }
}

.page-header__logo img {
    @media (min-width: $desktop-width) {
        width: 187px;
        height: 97px;
    }
}

@media (min-width: $tablet-width) {
    .main-nav__toggle {
        display: none;
    }
}

.header-content {
    width: 100%;
    background-image: url(../../img/bcg-header-mobile.png);
    background-repeat: no-repeat;
    clip-path: polygon(0 0,100% 0,100% 92%,50% 99%,0 92%);
    margin: 0 auto;
    padding-bottom: 86px;
    @media (min-resolution: 2dppx) {
        background-image: url(../../img/bcg-header-mobile@2x.png);
    }
}

.header-content__img {
    display: block;
    margin: 0 auto;
}

.header-content__line1 {
    padding-top: 90px;
    margin-bottom: 4px;
}

.header-content__line2 {
    margin-bottom: 34px;
}

.header-content__line3 {
    margin-bottom: 25px;
}

.header-content__line4 {
    margin-right: 75px;
}

@media (min-width: $tablet-width) {
    .header-content {
        background-image: url(../../img/bcg-header-tablet.png);
        background-repeat: no-repeat;
        padding-bottom: 130px;
        @media (min-resolution: 2dppx) {
            background-image: url(../../img/bcg-header-tablet@2x.png);
            background-size: 768px auto;
        }
    }
    .header-content__line1 {
        width: 188px;
        height: 45px;
        padding-top: 77px;
        margin-bottom: 10px;
    }
    .header-content__line2 {
        width: 257px;
        height: 31px;
        margin-bottom: 49px;
    }
    .header-content__line3 {
        width: 461px;
        height: 80px;
        margin-bottom: 54px;
    }
    .header-content__line4 {
        width: 295px;
        height: 80px;
        margin-left: 226px;
    }
}

@media (min-width: $desktop-width) {
    .header-content {
        background-image: url(../../img/bcg-header-desktop.png);
        background-repeat: no-repeat;
        margin-top: -49px;
    }
    .header-content__line1 {
        padding-top: 77px;
    }
    .header-content__line4 {
        margin-left: 443px;
    }
    @media (min-resolution: 2dppx) {
        background-image: url(../../img/bcg-header-desktop@2x.png);
        background-size: 768px auto;
    }
}

.header-inner {
    min-height: 130px;
    background: url(../../img/bcg-header-mobile-inner.png);
    clip-path: polygon(0 0,100% 0,100% 83%,50% 99%,0 83%);
    background-repeat: no-repeat;
    padding-bottom: 29px;
    @media (min-resolution: 2dppx) {
        background-image: url(../../img/bcg-header-mobile-inner@2x.png);
        background-size: 320px;
    }
    @media (min-width: $tablet-width) {
        min-width: $tablet-width;
        min-height: 216px;
        background-image: url(../../img/bcg-header-tablet-inner.png);
        background-position: 0 -58px;
        clip-path: none;
        padding-bottom: 130px;
        @media (min-resolution: 2dppx) {
            background-image: url(../../img/bcg-header-tablet-inner@2x.png);
        }
    }
    @media (min-width: $desktop-width) {
        min-height: 227px;
        background-image: url(../../img/bcg-header-desktop-inner.png);
        padding-bottom: 159px;
        @media (min-resolution: 2dppx) {
            background-image: url(../../img/bcg-header-desktop-inner@2x.png);
        }
    }
}

.header-form {
    @media (min-width: $tablet-width) {
        padding-bottom: 97px;
    }
}

.header-inner__img {
    display: block;
    margin: 0 auto;
    padding-top: 52px;
    @media (min-width: $tablet-width) {
        width: 459px;
        height: 85px;
        padding-top: 89px;
    }
    @media (min-width: $desktop-width) {
        width: 460px;
        height: 80px;
        padding-top: 101px;
    }
}
